import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import login from './views/login.vue'
import enter from './views/enter.vue'
import mint from './views/mint.vue'
import logins from './views/login/logins.vue'
import ditu from './views/ditu/ditu.vue'
import baojinA from './views/baojing/baojinA.vue'
import gengduoA from './views/gengduo/gengduoA.vue'
import jibenxinxi from './views/gengduo/jibenxinxi.vue'
import jiankongA from './views/jiankong/jiankongA.vue'
import form from './views/baobiao/form.vue'
import baobiao from './views/baobiao/baobiao.vue'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			name: 'logins',
			component: logins
		},
		{
			path: '/enter',
			name: 'enter',
			component: enter
		},
		{
			path: '/ditu',
			name: 'ditu',
			component: ditu,
		},
		{
			path: '/baojinA',
			name: 'baojinA',
			component: baojinA
		},
		{
			path: '/baobiao',
			name: 'baobiao',
			component: baobiao
		},
		{
			path: '/jiankongA',
			name: 'jiankongA',
			component: jiankongA,

		},
		{
			path: '/gengduoA',
			name: 'gengduoA',
			component: gengduoA,
		},
		{
			path: '/jibenxinxi',
			name: 'jibenxinxi',
			component: jibenxinxi,
		},
		{
			path: '/form',
			name: 'form',
			component: form,
		},
	]
})
