<template>
	<div style="text-align: center;margin-top: ;">
		<heading title="注册页面"></heading>
		<div style="height: 50px;"></div>
		<mu-container>
			<mu-text-field v-model="todo.value1" placeholder="姓名"></mu-text-field><br />
			<mu-text-field v-model="todo.value2" placeholder="身份证号码"></mu-text-field><br />
			<mu-text-field v-model="todo.value3" placeholder="家庭地址"></mu-text-field><br />
			<mu-text-field v-model="todo.value4" placeholder="移动电话"></mu-text-field><br />
			<mu-text-field v-model="todo.value5" label="邮箱" suffix="@qq.com"></mu-text-field><br />
			<mu-text-field v-model="todo.value6" label="用户名" prefix="" label-float></mu-text-field><br />
			<mu-text-field v-model="todo.value7" label="密码" :action-icon="visibility ? 'visibility_off' : 'visibility'"
			 :action-click="() => (visibility = !visibility)" :type="visibility ? 'text' : 'password'"></mu-text-field><br />
			 <mu-dialog title="注册成功" width="360" :open.sync="openSimple">
			 	三秒后自动跳转登录页面o(∩_∩)o 
			 </mu-dialog>
			 <mu-dialog title="还有未填选项" width="360" :open.sync="openSimpleA">
			 请全部填选后点击提交~
			 </mu-dialog>
			 <mu-dialog title="取消成功" width="360" :open.sync="openSimpleB">
			 数据已清空，请重新输入~
			 </mu-dialog>
		</mu-container>
		<div>
			<mu-button class="demo-color-btn" style="margin-right: 30px;" color="primary" @click="tijiao">提交</mu-button>
			<span><mu-button class="demo-color-btn" color="success" @click="quxiao">取消</mu-button></span>	
		</div>
		

			
		
				<!-- <div class="text-A">tcx</div>
		<div class="sanjiao"></div>
		<div class="ys-A"></div>
		<ul class="list">
			<li><span>我是测试文字</span>2018/12/4</li>
			<li><span>我是测试文字</span>2018/12/4</li>
			<li><span>我是测试文字</span>2018/12/4</li>
			<li><span>我是测试文字</span>2018/12/4</li>
			<li><span>我是测试文字</span>2018/12/4</li>
			<li><span>我是测试文字</span>2018/12/4</li>

		</ul> -->
	</div>
</template>

<script>
	import heading from '@/components/head.vue'
	export default {
		data() {
			return {
				todo: {
					value1: '',
					value2: '',
					value3: '',
					value4: '',
					value5: '',
					value6: '',
					value7: '',
				},
				name: null,
				sfz: null,
				dh: null,
				visibility: false,
				openSimple: false,
				openSimpleA: false,
				openSimpleB: false,
			}
		},
		components: {
			heading,
		},
		mounted() {

		},
		created() {

		},
		methods: {
			tijiao() {
				let todo = this.todo
				if (todo.value1 != '' && todo.value2 != '' && todo.value3 != '' && todo.value4 != '' && todo.value5 != '' && todo.value6 !=
					'' && todo.value7 != '') {
					for (let i = 0; i < 7; i++) {
						switch (i) {
							case 0:
								var reg = /^[\u4E00-\u9FA5\uf900-\ufa2d·s]{2,4}$/;
								if (reg.test(this.todo.value1)) {
									// console.log('姓名正确')
								} else {
									this.name = '请输入有效姓名'
								}
								break;
							case 1:
								let reg1 = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
								if (reg1.test(this.todo.value2)) {
									// console.log('身份证正确')
								} else {
									this.sfz = '请输入有效身份证'
								}
								break;
							case 3:
								let reg2 = /^1(3|4|6|7|8)\d{9}$/;
								if (reg2.test(this.todo.value4)) {
									// console.log('电话正确')
								} else {
									this.dh = '请输入有效电话'
								}
								break;
							default:
								break;
						}
					}
					if (this.name !== '请输入有效姓名' && this.sfz !== '请输入有效身份证' && this.dh !== '请输入有效电话') {
						localStorage.setItem(`${this.todo.value6}`, JSON.stringify(this.todo))
                       this.openSimple = true;
						setTimeout(()=>{
							this.$router.push('/')
						},3000)
					} else {
						console.log(`${this.name}${this.sfz}${this.dh}`)
					}
				} else {
					// console.log('还有未填项')
					this.openSimpleA = true 
					setTimeout(()=>{
						this.openSimpleA = false
					},2000)
				}
			},
			quxiao(){
				this.todo.value1=''
				this.todo.value2=''
				this.todo.value3=''
				this.todo.value4=''
				this.todo.value5=''
				this.todo.value6=''
				this.todo.value7=''
				this.openSimpleB = true 
				setTimeout(()=>{
					this.openSimpleB = false
				},2000)
			}
		}

	}
</script>

<style scoped="scoped" lang="less">
	@text: red; //变量属性

	.text-A {
		color: @text;
		.body; //混合属性
		// .body-2(3px);
		.body-3();
	}

	.body {
		height: 100px;
		width: 100px;
		background: #FFFF00;
	}

	.body-2(@width) {
		//混合带参数
		border: @width solid #2196F3;
	}

	.body-3(@width: 1px) {
		//混合带默认参数
		border: @width solid #42B983;
	}

	.sanjiao {
		.body-4(2); //匹配模式 变量名可以是一样的
	}

	.body-4(1, @a: 0, @b: 0, @c: 10px) {
		width: @a;
		height: @b;
		overflow: hidden;
		border-width: @c;
		border-color: transparent transparent #FFFF00 transparent;
		border-style: dashed dashed solid dashed;
		margin-bottom: 20px;
	}

	.body-4(2, @a: 0, @b: 0, @c: 20px) {
		width: @a;
		height: @b;
		overflow: hidden;
		border-width: @c;
		border-color: transparent transparent #FFFF00 transparent;
		border-style: dashed dashed solid dashed;
		margin-bottom: 20px;
	}

	@yunsuan: 50px;


	.ys-A {
		width: @yunsuan + 100; //运算功能 可以实现变量的加减乘除 并且可以加括号先运行加减运算功能 做到灵活多变
		background: #42B983;
		height: 30px;
	}

	.list {
		width: 70%;
		padding-top: 5px;
		background: #E6E6E6;


		li {
			height: 30px;
			margin: 5px;
		}

		span {
			&:hover {
				color: red;
			}
		}
	}
</style>
