<template>
	<div class="kz">
		<!-- <div class="xkz"> -->
		<!--地图容器-->
		<baidu-map :center="center" :zoom="zoom"     @ready="handler" class="xkz" :scroll-wheel-zoom="false">
			<!-- 上侧输入框 -->
			<bm-control class="text-a-s" style="margin-top: 8px;">
				<div class="text-a" v-if="inputA">
					<div class="text-a-1">
						<img src="../../../image/map/l_jiantou.png" alt="" @click="returna">
					</div>
					<div class="text-a-2">
						<input id="input" style="padding: 8px;color: #666666;" type="text" placeholder="查找污染源企业" v-model="companyName">
					</div>
				</div>
				<div class="text-a" v-if="inputA==false">
					<input id="input" style="padding: 8px;color: #666666;" type="text" placeholder="查找污染源企业"  @focus="inputFocus">
				</div>
				<div class="qusi" v-if="inputB">
					
				<div class="text-a-A" >
					<div v-for="(todo,index) in listdataB" @click="chooseSearch(index)">
						<img src="../../../image/control/qy_tb.png" alt="">
						<div>{{todo.companyName}}</div>
					</div>
				</div>
				</div>
			</bm-control>
			<!-- 右侧 -->
			<bm-control class="text-b" anchor="BMAP_ANCHOR_TOP_RIGHT">
				<div class="text-b-1" v-if="shuju1">
					<div v-show="shuju2" >
						<!-- <div></div> -->
						<span></span>
						<div class="bb">
							<div><img src="../../../image/map/8-zhengc.png" alt="">
								<div>正常({{zc}})</div>
							</div>
							<div><img src="../../../image/map/7-yic.png" alt="">
								<div>异常({{yc}})</div>
							</div>
							<div><img src="../../../image/map/6-yuj.png" alt="">
								<div>预警({{yj}})</div>
							</div>
							<div><img src="../../../image/map/5-baoj.png" alt="">
								<div>超标({{cb}})</div>
							</div>
							<div><img src="../../../image/map/1-gz.png" alt="">
								<div>故障({{gz}})</div>
							</div>
							<div><img src="../../../image/map/3-lix.png" alt="">
								<div>离线({{lx}})</div>
							</div>
							<div><img src="../../../image/map/4-gf.png" alt="">
								<div>停运({{ty}})</div>
							</div>
							<div></div>
						</div>
					</div>
					<div  v-show="shuju2===false">
						<span></span>
						<div>	
							<div></div>
						</div>
					</div>
					<div class="text-b-2" @click="shuju2=!shuju2"><img src="../../../image/map/tuli.png" alt=""></div>
				</div>
				<div>
				</div>
			</bm-control>
			<!-- 缩放 -->
			<bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT" :offset='{width: 10,height: 480}'></bm-navigation>
			<!-- 卫星图 -->
			<bm-map-type :map-types="['BMAP_NORMAL_MAP', 'BMAP_HYBRID_MAP']" anchor="BMAP_ANCHOR_TOP_LEFT" :offset='{width: 5,height: 650}'></bm-map-type>
			<!-- 定位 -->
			<bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true"></bm-geolocation>
			<!-- 版权信息 -->
			<bm-copyright anchor="BMAP_ANCHOR_TOP_RIGHT" :copyright="[{id: 1, content: 'Copyright Message', bounds: {ne: {lng: 110, lat: 40}, sw:{lng: 0, lat: 0}}}, {id: 2, content: '<a>版权:汤晨旭,练习用</a>'}]">
			</bm-copyright>
			<!-- 自定义覆盖物 -->
			 <my-overlay
			  v-for="(todo,index) in listdataA"
			  :position="{lng:Number(todo.longitude.longitude), lat:Number(todo.latitude.latitude)}"
			  text="点击我"
			  :img = "iconUrl(index)"
			  :active="active">
			</my-overlay>
		</baidu-map>
		<!-- </div> -->
		<dibuchajian index=2></dibuchajian>
	</div>
</template>

<script>
	import dibuchajian from './dibuchajian'
	import myOverlay from './myOverlay.vue'
	export default {
		data() {
			return {
				  active: false,
				center:{
					lng: 120.86,
					lat: 32.01,
				},
				centerx:{
					lng: 120.86,
					lat: 32.01,
				},
				zoom: 13,
				inputA: false,
				listdataA:[],
				listdataB:[],
				zc: 0,
				yc: 0,
				yj: 0,
				cb: 0,
				gz: 0,
				lx: 0,
				ty: 0,
				shuju1:true,
				companyName:'',
				inputB:false,
				shuju2:true,
				lng:null,
				lat:null,
				cs:0,
			};
		},
		components: {
			dibuchajian,
			 myOverlay,
			// BaiduMap,
		},
	computed: {
        
    },
		mounted() {
          this.shuju();
		 this.center.lng = 120.86
		 this.center.lat = 32.01
		},
		 watch: {
        //监听输入框渲染搜索框数据
         inputA(v){
			 if(v===false){
				 this.shuju1 = true
			 }
		 }
    },
		methods: {
			//选择图片
			iconUrl(index) {
						let number = this.listdataA[index].status
			            switch (number) {
			                // 停运
			                case '0': return require("../../../image/map/4-gf.png");
			                // 离线
			                case '1': return require("../../../image/map/3-lix.png");
			                // 故障
			                case '2': return require("../../../image/map/1-gz.png");
			                // 超标
			                case '4': return require("../../../image/map/5-baoj.png");
			                // 预警
			                case '5': return require("../../../image/map/6-yuj.png");
			                // 异常
			                case '3': return require("../../../image/map/7-yic.png");
			                case '6': return require("../../../image/map/7-yic.png");
			                // 正常
			                case '9': return require("../../../image/map/8-zhengc.png");
							case '7': return require("../../../image/map/8-zhengc.png");
							case '8': return require("../../../image/map/8-zhengc.png");
			
			                default: break;
			            }
			        },
			//请求数据
			shuju() {
				this.$http.get('/ditu').then((res) => {
					this.listdataA = res.data.data
					this.listdataB = this.listdataA
					this.listdataB.length=8
					console.log(this.listdataA)
					// 得到报警数据
					for (let val of this.listdataA) {
						var a=Number(val.status)
						switch (a) {
						 // 停运
                        case 0: this.ty++;this.cs=0; break;
                        // 离线
                        case 1: this.lx++;this.cs=1; break;
                        // 故障
                        case 2: this.gz++;this.cs=2; break;
                        // 超标
                        case 4: this.cb++;this.cs=4; break;
                        // 预警
                        case 5: this.yj++;this.cs=5; break;
                        // 异常
                        case 3: this.yc++;this.cs=3; break;
                        case 6: this.yc++;this.cs=6; break;
                        // 正常
                        case 9: this.zc++;this.cs=9; break;
						case 8: this.zc++;this.cs=9; break;
                        case 7: this.zc++;this.cs=9; break;

                        default: break;
						}
					}
					console.log(this.ty)
				})
			},
			//点击提示栏显示的数据到输入框的方法
			chooseSearch(index){
				console.log(index)
				this.cs= index
				this.companyName = this.listdataA[index].companyName
			    this.inputA = true //输入框
				this.inputB = false//提示选择框
				this.shuju1 = true//展示数据框
				this.center.lng = Number(this.listdataA[index].longitude.longitude)
				this.center.lat =Number(this.listdataA[index].latitude.latitude	) 
				this.lng = Number(this.listdataA[index].longitude.longitude)
				this.lat = Number(this.listdataA[index].latitude.latitude	) 
				this.centerx = this.center
				console.log(this.center)
				 this.zoom = 19
			},
			//点击返回箭头
			returna(){
			   	this.companyName = ''
				this.inputA = false //输入框
				// this.inputB = true//提示选择框
				this.shuju1 = false//展示数据框
				this.center.lng = 120.86
				this.center.lat = 32.01
			    this.zoom = 13
			},
			//点击聚焦
			inputFocus(){
				this.inputB = true
				this.shuju1 = false
			},
			handler({
				BMap,
				map
			}) {
				console.log(BMap, map)
			},
		}
	}
</script>

