<template>
	<div class="kz">
		<div class="xkz">
			<div class="text-a">
				<p>报警中心</p>
			</div>
			<div class="text-b">
				<div class="text-b-a" style="margin-left: 10px;">
					<div>近7日报警数量统计<span style="margin-left: 10px; color: #ffd800;">58</span></div>
				</div>
				<div style="margin-right: 10px">报警记录</div>
			</div>
			<div class="from">
				<!-- 表单图 -->
				<div class="from-bd-1" id="myChart">

				</div>
			</div>
			<div class="from-a">
				<div @click="cb(index)" v-for="(todo,index) in todoindexa">
					<div class="from-a-1" :class="{'aaa':index===0,'a2':index===1,'a3':index===2,'a4':index===3}">
						<p>{{todo.number1}}</p>
					</div>
					<p>{{todo.name}}</p>
				</div>
			</div>
			<div class="he">
				<div class="from-b-1" v-for="(todo,index) in lista">
					<div class="from-b-2">
						<div>{{index}}</div><span>{{todo}}</span>
					</div>
					<div class="from-b-3">废水</div>
				</div>
			</div>
		</div>
		<dibuchajian :index='number'></dibuchajian>
	</div>
</template>

<script>
	import dibuchajian from '../ditu/dibuchajian'
	// import {obj} from './map.js'
	export default {
		data() {
			return {
				number: 0,
				index:true,
				lista:[],
				listb:[],
				listc:[],
				listd:[],
				todoindexa:[
					{name:'超标',number1:0,},
					{name:'离线',number1:0,},
					{name:'故障',number1:0,},
					{name:'其他',number1:0,},
				],
				listdataA: [],
				listdataB: [],
				listdataC: [],
				listdataD: [],
				timeArray: [], //时间
				times: [],
				data1: [],
				data2: [],
				data3: [],
				data4: [],
				indexbus:7,
				pand: true,
				num:{
				},
				ys1:false,
				ys2:false,
				ys3:false,
				ys4:false,	
			};
		},
		components: {
			dibuchajian
		},
		mounted() {
			this.shuju1();
            
		},
		methods: {
			//获取数据
			shuju1() {
				this.$http.get('/baojingB').then((resa) => {
					this.listdataB = resa.data.data
					console.log(this.listdataB)
				})
				this.$http.get('/baojingc').then((resa) => {
					this.listdatac = resa.data.data
					console.log('q',this.listdatac)
				})
				this.$http.get('/baojingd').then((resa) => {
					this.listdatad = resa.data.data
					console.log('w',this.listdatad)
				})
				this.$http.get('/baojingA').then((res) => {
					this.listdataA = res.data.data
					console.log(this.listdataA)

					this.timeA() //处理时间
					this.numbera()
					console.log(this.data1)
					this.sizea();
					this.detail(1,2,3);
					this.drawLine() //启动图表放在最后
				})
				
			},
			//转化时间
			timeA() {
				let timea = this.listdataA[0].lineChart.time
				for (let val of timea) {
					var a = val.slice(5)
					this.timeArray.push(a)
				}
			},
			//处理显示的数据
			numbera() {
				this.data1 = this.listdataA[0].lineChart.data1
				this.data2 = this.listdataA[0].lineChart.data2
				this.data3 = this.listdataA[0].lineChart.data3
				this.data4 = this.listdataA[0].lineChart.data4
				this.times = this.listdataA[0].lineChart.time
			},
			//通用方法
			huoqu(a,b,c,arr){
				 for(let val of arr){
					    	console.log(val)					    	
					    	switch(Number(val.type)){
					    		case a:case b:case c:
					    		this.lista.push(val.monitorName)
					    		console.log('222',this.lista)
					    		break;
					    	}
					    }
			},
			//处理显示内容的通用方法
			detail(a,b,c){
				this.lista = []//初始化
				console.log('222')
				
				switch(this.indexbus){
					case 1:case 3:case 6:
					let arr = this.listdataB
					   this.huoqu(a,b,c,arr)
					break;
					case 2:case 5:
					let arr1 = this.listdatac
						this.huoqu(a,b,c,arr1)
					break;
					case 4:case 7:
					let arr2 = this.listdatad
						this.huoqu(a,b,c,arr2)
					break;
				}			
			},
			//处理显示内容
			cb(index){
				switch(index){
					case 0:this.detail(1,2,3);break;
					case 1:this.detail(4,5,6);break;
					case 2:this.detail(7,7,8);break;
					case 3:this.detail(9,9,9);break;
					default:break;
				}				
			},
			 sizea(){
				// console.log(val)
				this.todoindexa[0].number1 = this.listdataA[0].lineChart.data1[6]
				this.todoindexa[1].number1 = this.listdataA[0].lineChart.data2[6]
				this.todoindexa[2].number1 = this.listdataA[0].lineChart.data3[6]
				this.todoindexa[3].number1 = this.listdataA[0].lineChart.data4[6]
				 console.log(this.todoindexa[0].number1)
			},
			drawLine() {
				// 基于准备好的dom，初始化echarts实例
				var that = this;
				let myChart = this.$echarts.init(document.getElementById('myChart'))
				// 绘制图表
				const option = {
					title: {
						text: '连续七日报警统计',
						x: 'center',
						y: '9px',
						textStyle: {
							fontSize: 18,
							// fontWeight: 'bolder',
							color: '#ffffff'
						}
					},
					color: ['#e95959', '#8f8f8f', '#e99324', '#96cdea', ],
					tooltip: {
						triggerOn: 'click',
						trigger: 'axis',
						axisPointer: {
							type: 'line',
							lineStyle: {
								color: '#fff',
								width: 1
							},
							crossStyle: {
								color: '#999999',
								type: 'solid',
								width: 3,
								shadowColor: 'rgba(0,1,2,0)',
							},
						},
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						data: that.timeArray,
						axisLine: {
							show: true,
							onZero: true,
							lineStyle: {
								color: ['#ffffff']
							},
						},
						axisTick: {
							show: false,
							interval: 'auto',
							onGap: null,
							inside: false,
							length: 0,
						},
					}],
					yAxis: [{
						/*y轴数字隐藏*/
						axisLabel: {
							show: false
						},
						boundaryGap: false,
						/*Y轴的坐标线和文字之间的连接符*/
						axisTick: {
							show: false,
						},
						/*Y轴的坐标线*/
						axisLine: {
							show: false,
						},
						type: 'value',
						// 分割线设置
						/*Y轴的分割线*/
						splitLine: {
							show: true,
							lineStyle: {
								color: ['#6a9cea']
							}
						},
					}],
					series: [{
							name: '超标',
							type: 'bar',
							stack: '广告',
							barWidth: 22,
							data: that.data1

						},
						{
							name: '离线',
							type: 'bar',
							stack: '广告',
							barWidth: 22,
							data: that.data2
						},
						{
							name: '故障',
							type: 'bar',
							stack: '广告',
							barWidth: 22,
							data: that.data3
						},
						{
							name: '其它',
							type: 'bar',
							stack: '广告',
							barWidth: 22,
							data: that.data4
						}
					]
				}
				myChart.setOption(option);
				myChart.on('click', function(params) { //实例方法 可以通过params参数来控制图标进行操作
				//统计四个圆点的通用方法
			    function size(val){
					console.log(val)
					that.todoindexa[0].number1 = that.listdataA[0].lineChart.data1[val]
					that.todoindexa[1].number1 = that.listdataA[0].lineChart.data2[val]
					that.todoindexa[2].number1 = that.listdataA[0].lineChart.data3[val]
					that.todoindexa[3].number1 = that.listdataA[0].lineChart.data4[val]
				};
					console.log(params.dataIndex)
					let a = params.dataIndex+1
					that.indexbus = a 
					switch(a){
						case 1:size(0);that.detail(1,2,3);break;
						case 2:size(1);that.detail(1,2,3);break;
						case 3:size(2);that.detail(1,2,3);break;
						case 4:size(3);that.detail(1,2,3);break;
						case 5:size(4);that.detail(1,2,3);break;
						case 6:size(5);that.detail(1,2,3);break;
						case 7:size(6);that.sizea();that.detail(1,2,3);break;
						default: break;
					}
// 					switch(index){
// 						case 0:this.detail(1,2,3);break;
// 						case 1:this.detail(4,5,6);break;
// 						case 2:this.detail(7,7,8);break;
// 						case 3:this.detail(9,9,9);break;
// 						default:break;
// 					}
					
				});
				/*----------------分割线------------------- */
					app.currentIndex = 1;
					setTimeout(function() { // 这里解决了初始化自动点击事件
						var dataLen = option.series[0].data.length;
						// 取消之前高亮的图形
						myChart.dispatchAction({
							type: 'downplay',
							seriesIndex: 0,
							dataIndex: app.currentIndex
						});
						app.currentIndex = 6;
						// 高亮当前图形
						myChart.dispatchAction({
							type: 'highlight',
							seriesIndex: 0,
							dataIndex: app.currentIndex
						});
						// 显示 tooltip
						myChart.dispatchAction({
							type: 'showTip',
							seriesIndex: 0,
							dataIndex: app.currentIndex
						});
					}, 300);



			}
		},
	}
</script>

