<template>
	<div>
		<heading title="登录页面" />
		<div style="height: 80px;"></div>
		<mu-container>
			<mu-form ref="form" :model="validateForm" class="mu-demo-form">
				<mu-form-item label="用户名" help-text="帮助文字" prop="username" :rules="usernameRules">
					<mu-text-field v-model="validateForm.username" prop="username"></mu-text-field>
				</mu-form-item>
				<mu-form-item label="密码" prop="password" :rules="passwordRules">
					<mu-text-field type="password" v-model="validateForm.password" prop="password"></mu-text-field>
				</mu-form-item>
				<mu-form-item prop="date" label="登录时间">
					<mu-date-input v-model="validateForm.date" type="dateTime" actions></mu-date-input>
				</mu-form-item>、
				<mu-form-item prop="isAgree" :rules="argeeRules">
					<mu-checkbox label="同意用户协议" v-model="validateForm.isAgree"></mu-checkbox>
				</mu-form-item>
				<mu-form-item style="margin-left: 40%;margin-top: 80px;">
					<mu-button color="primary" @click="submit">提交</mu-button>
					<mu-button @click="clear">重置</mu-button>
				</mu-form-item>
			</mu-form>
		</mu-container>
	</div>
</template>

<script>
	import heading from '@/components/head.vue'
	export default {
		data() {
			return {
				usernameRules: [{
						validate: (val) => !!val,
						message: '必须填写用户名'
					},
					{
						validate: (val) => val.length >= 3,
						message: '用户名长度大于3'
					}
				],
				passwordRules: [{
						validate: (val) => !!val,
						message: '必须填写密码'
					},
					{
						validate: (val) => val.length >= 3 && val.length <= 10,
						message: '密码长度大于3小于10'
					}
				],
				argeeRules: [{
					validate: (val) => !!val,
					message: '必须同意用户协议'
				}],
				validateForm: {
					username: '',
					password: '',
					isAgree: false,
					date:'',
				}
			}
		},

		components: {
			heading,
		},
		mounted() {

		},
		created() {

		},
		methods: {
			submit() {
				this.$refs.form.validate().then((result) => {
					if (result) {
					  this.$toast.success('登录成功');
					} else{
					this.$toast.error('登录失败');
					}
					console.log('form valid: ', result)
				});
			},
			clear() {
				this.$refs.form.clear();
				this.validateForm = {
					username: '',
					password: '',
					isAgree: false
				};
			}
		}

	}
</script>

<style scoped="scoped">
@import '//fonts.useso.com/css?family=Roboto:300,400,500,700,400italic';
@import 'http://cdn.bootcss.com/material-design-icons/3.0.1/iconfont/material-icons.css';

</style>